<template>
  <van-popup class="derify-popup" v-model="showPopup" round :closeable="false" @close="close">
    <div class="system-popup">
      <div class="system-popup-title"></div>
      <div class="fz-15" style="margin-top: 1rem">
        <span class="fc-65"></span>
        <span class="fc-yellow"></span>
        <span class="fc-65"></span>
        <span class="fc-yellow"></span>
      </div>
      <div class="system-popup-buttons">
        <div class="system-popup-button cancel" @click="close"></div>
        <div class="system-popup-button confirm" @click="close"></div>
      </div>
    </div>
  </van-popup>
</template>

<script>
export default {
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      showPopup: this.show
    }
  },
  watch: {
    show () {
      this.showPopup = this.show
    }
  },
  methods: {
    close () {
      this.$emit('closeOneKeyUnwindPopup', false)
    }
  }
}
</script>

